<template>
  <div id="sub_wrap">
    <h1>입주사 방문 할인</h1>
    <div class="content_wrap">
      <div class="menu_title">
        <form>
          <span class="small_title">날짜</span>
          <input
            type="datetime-local"
            id="start_time"
            v-model="start_time"
            placeholder="2020.03.20 오전 12:00"
          />
          <span>~</span>
          <input
            type="datetime-local"
            id="end_time"
            v-model="end_time"
            placeholder="2020.03.20 오후 11:59"
          />

          <span class="small_title">차량번호</span>
          <input
            class="search_icon "
            type="text"
            placeholder="차량 번호를 입력해 주세요."
            maxlength="9"
            v-model="seachNumber"
          />
          <button class="seach_confirm" @click.prevent="lp_search">검색</button>
        </form>
      </div>
      <div class="search_wrap">
        <h2>검색된 차량</h2>
        <ul>
          <li v-for="(c, index) in vehicleInformation" :key="index">
            <div class="carPicture"><img :src="c.image_name" alt="" /></div>
            <div class="vehicleinfo">
              <div>
                <p>차량번호<input type="text" v-model="c.lp" /></p>
                <p>
                  입차시각(사람)<span>{{ c.time }}</span>
                </p>
                <p>
                  메모<input
                    type="text"
                    v-on:change="contents_change(index)"
                    v-model="c.memo"
                  />
                </p>
              </div>
            </div>
            <div class="timesetting">
              <button
                @click="discount_time(c, btn)"
                v-for="(btn, index) in buttonTime"
                :key="index"
              >
                <span>{{ btn.text }}</span>
              </button>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../config.js"
var image_src = config.image_src
console.log("image_src : ", image_src)
export default {
  data() {
    return {
      start_time: new Date(),
      end_time: new Date(),
      seachNumber: "",
      id: "",
      dafault_free_parking_time: "60",
      free_parking_time: "60",
      vehicleInformation: [
        /* {
          lp: "12자1234",
          loop_event_time: 1586297735243,
          time: format_time1(new Date(1586297735243)),
          memo: "초기데이터",
          image_name: require("../assets/car_test1.jpg") 
        } */
      ],
      buttonTime: [
        // "무료",
        // "60",
        // "90",
        // "120",
        // "150",
        // "180",
        // "240",
        // "300",
        // "360",
        // "전액"
      ],
      member_list: []
    }
  },
  methods: {
    socket_process(data) {
      console.log("===> : ", data)
      if (data.kind === "get_resident_policy_list") {
        console.log(data)
        this.default_free_parking_time = data.docs[0].free_parking_time
        this.buttonTime = []
        // let temp_obj = {}
        //  temp_obj.text = "무료"
        //  this.buttonTime.push(temp_obj)
        for (
          let i = 0;
          i < data.docs[0].resident_discoun_button_list.length;
          i++
        ) {
          let obj = data.docs[0].resident_discoun_button_list[i]
          let obj_temp = {}
          if (obj.value === true) {
            let text = obj.text.replace("유료", "")
            text = text.replace("분", "")
            obj_temp.text = text
            console.log("obj.text : ", obj, obj_temp)
            this.buttonTime.push(obj_temp)
          }
        }
      }
      if (data.kind === "get_resident_list") {
        this.id = window.localStorage.getItem("id")
        console.log("get_resident_list : ", data, this.id, data.docs[0].id)
        for (let i = 0; i < data.docs.length; i++) {
          if (this.id === data.docs[i].id) {
            if (data.docs[i].free_parking_time) {
              this.free_parking_time = data.docs[i].free_parking_time
            } else {
              this.free_parking_time = this.default_free_parking_time
            }
            break
          }
        }
      } else if (data.status === "ok" && data.kind === "how_much") {
        if (data.docs.length !== 0 && data.docs !== null) {
          console.log(
            "socket_process for before_kim: ",
            this.seachNumber,
            data.docs,
            data.docs.length
          )
          this.vehicleInformation = []
          for (let i = 0; i < data.docs.length; i++) {
            console.log(
              "socket_process for before: ",
              this.seachNumber,
              data.docs
            )
            if (
              data.docs[i].kind === "lpr" ||
              data.docs[i] === "fee_calculation_result"
            ) {
              // if(this.seachNumber ===""){
              console.log(
                "socket_process for ===> seachNumber: ",
                this.seachNumber,
                data.docs
              )
              let image_name =
                image_src + data.docs[i].folder_name + "/" + data.docs[i].fname
              let obj = {}
              obj.lp = data.docs[i].lp
              obj.loop_event_time = data.docs[i].loop_event_time
              obj.time = format_time1(new Date(data.docs[i].loop_event_time))
              obj.image_name = image_name
              obj.free_parking_time = data.docs[i].free_parking_time
              obj.memo = data.docs[i].contents
              this.vehicleInformation.push(obj)
            }
          }
        }
      }
    },
    lp_search() {
      this.vehicleInformation = []
      var obj = {}
      obj.kind = "how_much"
      if (this.seachNumber !== "") {
        obj.lp = this.seachNumber
      }
      obj.start_time = get_start_time(this.start_time)
      obj.end_time = get_end_time(this.end_time)
      obj.id = window.localStorage.getItem("id")
      obj.pw = window.localStorage.getItem("pw")
      console.log("lp_search() : ", obj)
      this.sendMessage(obj)
    },
    date_set() {
      let d = new Date()
      var str_start =
        d.getFullYear() +
        "-" +
        itoStr(d.getMonth() + 1) +
        "-" +
        itoStr(d.getDate()) +
        "T00:00"
      var str_end =
        d.getFullYear() +
        "-" +
        itoStr(d.getMonth() + 1) +
        "-" +
        itoStr(d.getDate()) +
        "T23:59"
      this.start_time = str_start
      this.end_time = str_end
    },
    contents_change(index) {
      console.log("내용변경 :", this.vehicleInformation[index])
      var obj = {}
      // obj.kind = "update_vehicle_obj_at_vehicle_obj_list"
      obj.kind1 = "update_vehicle_obj_at_vehicle_obj_list"
      // obj.lp = this.vehicleInformation[index].lp
      obj.loop_event_time = this.vehicleInformation[index].loop_event_time
      obj.contents = this.vehicleInformation[index].memo
      console.log("obj : ", obj)
      this.sendMessage(obj)
    },
    discount_time(c, discount_time) {
      console.log("discounted_time : ", c, discount_time)
      // let free_parking_time = window.localStorage.getItem(
      //       "free_parking_time")
      this.$confirm(
        discount_time.text !== "무료"
          ? "무료" +
              this.free_parking_time +
              "분,  유료" +
              discount_time.text +
              "분 할인입니다. "
          : "무료" + this.free_parking_time + "분 할인입니다. ",
        "확인"
      ).then(resolve => {
        if (resolve === true) {
          if (discount_time.text == "무료") {
            console.log("discounted_time then 무료: ", c, discount_time.text)
            let obj = {}
            obj.id = window.localStorage.getItem("id")
            obj.pw = window.localStorage.getItem("pw")
            obj.loop_event_time = c.loop_event_time
            obj.lp = c.lp
            obj.discount_time = 0
            obj.kind = "resident_discounted_fee"
            obj.free_parking_time =
              parseInt(this.free_parking_time, 10) * 60 * 1000
            obj.total_discounted_parking_time = obj.free_parking_time
            this.$socket.emit("message_from_web_client", obj)
            console.log("obj : ", obj)
          } else if (discount_time.text == "전액") {
            console.log("discounted_time then 전액: ", c, discount_time.text)
            let obj = {}
            obj.id = window.localStorage.getItem("id")
            obj.pw = window.localStorage.getItem("pw")
            obj.loop_event_time = c.loop_event_time
            obj.lp = c.lp
            obj.discount_time = parseInt(10 * 24 * 60, 10) * 60 * 1000
            obj.kind = "resident_discounted_fee"
            obj.free_parking_time =
              parseInt(this.free_parking_time, 10) * 60 * 1000
            obj.total_discounted_parking_time =
              obj.discount_time + obj.free_parking_time
            this.$socket.emit("message_from_web_client", obj)
            console.log("obj : ", obj)
          } else {
            console.log("discounted_time then 유료: ", c, discount_time)
            let obj = {}
            obj.id = window.localStorage.getItem("id")
            obj.pw = window.localStorage.getItem("pw")
            obj.loop_event_time = c.loop_event_time
            obj.lp = c.lp
            obj.discount_time = parseInt(discount_time.text, 10) * 60 * 1000
            obj.kind = "resident_discounted_fee"
            obj.free_parking_time =
              parseInt(this.free_parking_time, 10) * 60 * 1000
            obj.total_discounted_parking_time =
              obj.discount_time + obj.free_parking_time
            this.$socket.emit("message_from_web_client", obj)
            console.log("obj : ", obj)
          }

          this.vehicleInformation = []
          this.seachNumber = ""
        }
      })
    },
    button_read() {
      let obj = {}
      obj.kind = "get_resident_policy_list"
      this.sendMessage(obj)
    },
    memberList() {
      let obj = {}
      obj.kind = "get_resident_list"
      this.sendMessage(obj)
    },
    sendMessage(c) {
      this.$socket.emit("message_from_web_client", c)
    },
    maessage() {
      alert
    }
  },
  created() {
    this.$socket.on("message_from_server", data => {
      console.log("data.kind : ", data.kind)
      this.socket_process(data)
    })
    this.date_set()
    this.button_read()
    this.memberList()
    //this.id = window.localStorage.getItem("id");
    //this.password = window.localStorage.getItem("pw");
    //console.log("id : ", this.id, "pw : ", this.password);
    //this.login_check(this.id, this.password);
    //window.addEventListener("scroll", this.handleScroll)
  }
}

function format_time1(dat) {
  var str = ""
  str += dat.getFullYear()
  str += "." + num_to_str(dat.getMonth() + 1)
  str += "." + num_to_str(dat.getDate())
  str += ". " + num_to_str(dat.getHours())
  str += ":" + num_to_str(dat.getMinutes())
  str += ":" + num_to_str(dat.getSeconds())
  return str
}

function num_to_str($num) {
  $num < 10 ? ($num = "0" + $num) : $num
  return $num.toString()
}

// function get_datetime(str) {
//   var res = str.split(/[-T.:\s]+/)
//   var d = new Date(
//     parseInt(res[0]),
//     parseInt(res[1]) - 1,
//     parseInt(res[2]),
//     parseInt(res[3]),
//     parseInt(res[4]),
//     0,
//     0
//   )
//   return d.getTime()
// }

function get_start_time(str) {
  var res = str.split(/[-T.:\s]+/)
  var d = new Date(
    parseInt(res[0]),
    parseInt(res[1]) - 1,
    parseInt(res[2]),
    parseInt(res[3]),
    parseInt(res[4]),
    0,
    0
  )
  return d.getTime()
}

function get_end_time(str) {
  var res = str.split(/[-T.:\s]+/)
  var d = new Date(
    parseInt(res[0]),
    parseInt(res[1]) - 1,
    parseInt(res[2]),
    parseInt(res[3]),
    parseInt(res[4]),
    59,
    999
  )
  return d.getTime()
}

function itoStr($num) {
  $num < 10 ? ($num = "0" + $num) : $num
  return $num.toString()
}
</script>

